import React, { useState, useEffect } from 'react'
import {Link, useNavigate, useParams} from 'react-router-dom'
import {Row,Col,Image,ListGroup,Card,Button} from 'react-bootstrap'
import {useSelector,useDispatch} from 'react-redux'
import Message from '../components/Message'
import Loader from '../components/Loader'
import {getOrderDetails,payOrder, deliverOrder} from '../actions/orderActions.js'
import axios from 'axios'
import {PayPalButton} from 'react-paypal-button-v2'
import { ORDER_PAY_RESET, ORDER_DELIVER_RESET } from '../constants/orderConstants'


const OrderScreen = () => {
    const dispatch=useDispatch()
    const navigate = useNavigate()
    const {id} = useParams()
    const [sdkReady,setSdkReady] = useState(false)

    const orderDetails = useSelector((state)=> state.orderDetails)
    const {order,error,loading} = orderDetails

    const orderPay = useSelector((state)=> state.orderPay)
    const {loading:loadingPay, success:successPay} = orderPay
    
    const orderDeliver = useSelector((state)=> state.orderDeliver)
    const {loading:loadingDeliver, success:successDeliver} = orderDeliver

    const userLogin = useSelector((state)=> state.userLogin)
    const {userInfo} = userLogin

    const addDecimals =(num) => {
        return (Math.round(num*100)/100).toFixed(2)
    }
    order.itemsPrice = addDecimals(order.orderItems.reduce((acc,item) => acc + item.qty*item.price ,0))

    useEffect(()=>{
        if(!userInfo){
            navigate('/login')
        }
        const addPayPalScript = async () => {
            const {data:clientId} = await axios.get('/api/config/paypal')
            const script = document.createElement('script')
            script.type = 'text/javascript'
            script.src=`https://www.paypal.com/sdk/js?client-id=${clientId}`
            script.async = true
            script.onload = () => {
                setSdkReady(true)
            }
            document.body.appendChild(script)
        }
        if(order.orderItems.length===0 || successPay || successDeliver){
            dispatch(getOrderDetails(id))
            dispatch({type:ORDER_PAY_RESET})
            dispatch({type:ORDER_DELIVER_RESET})
        }
        else if(!order.isPaid){
            if(!window.paypal){
                addPayPalScript()
            }
            else{
                setSdkReady(true)
            }
        }
        
    },[dispatch,navigate,successPay,id,order,userInfo,successDeliver])

    const successPaymentHandler = (paymentResult) => {
        console.log(paymentResult);
        dispatch(payOrder(id,paymentResult))
    }

    const deliverHandler = () => {
        dispatch(deliverOrder(order))
    }

    return loading ? <Loader /> : error ? <Message variant="danger">{error}</Message> : 
        <>
            <h2>ORDER {order._id}</h2>
            <Row>
            <Col md={8}>
                <ListGroup variant="flush">
                    <ListGroup.Item>
                        <h4>Shipping</h4>
                        <p>
                            <strong>Name: </strong>{order.user.name}
                        </p>
                        <p>
                            <strong>Email: </strong>
                            <a href={`mailto:${order.user.email}`}>{order.user.email}</a>
                        </p>
                        <p>
                            <strong>Address: </strong>
                            {order.shippingAddress.address}{" "},
                            {order.shippingAddress.city}{" "},
                            {order.shippingAddress.postalcode}{" "},
                            {order.shippingAddress.country}{" "}
                        </p>
                    
                        {order.isDelivered ? <Message variant="success">Delivered at {order.deliveredAt}</Message>
                                : <Message variant="danger">Not Delivered</Message>
                        }
                        
                    </ListGroup.Item>
   
                    <ListGroup.Item>
                        <h4>Payment Method</h4>
                        <p>
                            <strong>Method: </strong>
                            {order.paymentMethod} 
                        </p>
                        {order.isPaid ? <Message variant="success">Paid On {order.paidAt}</Message>
                                    : <Message variant="danger">Not Paid</Message>
                            }
                            
                    </ListGroup.Item>

                    <ListGroup.Item>
                        <h4>Orders: </h4>
                        {order.orderItems.length === 0 ? 
                            <Message>Your cart is empty</Message> : (
                            <ListGroup variant="flush">
                                {order.orderItems.map( (item,index) =>
                                    <ListGroup.Item key={index}>
                                        <Row>
                                            <Col md={1}>
                                                <Image src={item.image} alt={item.name} fluid="true"></Image>
                                            </Col>
                                            <Col>
                                                <Link to={`/products/${item.productId}`}>
                                                    {item.name}
                                                </Link>
                                            </Col>
                                            <Col md={4}>
                                                {item.qty} x ${item.price} = ${item.qty*item.price}
                                            </Col>
                                        </Row>
                                    </ListGroup.Item>

                                )}        
                            </ListGroup>
                        )}
                    </ListGroup.Item>
                </ListGroup>
            </Col>
            <Col md={4}>
                <Card>
                    <ListGroup variant="flush">
                        <ListGroup.Item><h4>Order Summary</h4></ListGroup.Item>
                        <ListGroup.Item>
                            <Row>
                                <Col>Items</Col>
                                <Col>${order.itemsPrice}</Col>
                            </Row>
                        </ListGroup.Item>

                        <ListGroup.Item>
                            <Row>
                                <Col>Shipping</Col>
                                <Col>${order.shippingPrice}</Col>
                            </Row>
                        </ListGroup.Item>

                        <ListGroup.Item>
                            <Row>
                                <Col>Tax</Col>
                                <Col>${order.taxPrice}</Col>
                            </Row>
                        </ListGroup.Item>

                        <ListGroup.Item>
                            <Row>
                                <Col>Total</Col>
                                <Col>${order.totalPrice}</Col>
                            </Row>
                        </ListGroup.Item>
                        {!order.isPaid && (
                            <ListGroup.Item>
                                {loadingPay && <Loader />}
                                {!sdkReady ? <Loader /> : (
                                    <PayPalButton amount={order.totalPrice}
                                    onSuccess={successPaymentHandler} />
                                )}
                            </ListGroup.Item>
                        )}
                        {loadingDeliver && <Loader />}
                        {userInfo && userInfo.isAdmin && order.isPaid && !order.isDelivered && (
                            <ListGroup.Item className='d-grid'>
                                <Button type="button" onClick={deliverHandler}>
                                    Mark As Deliver
                                </Button>
                            </ListGroup.Item>
                        )}
                    </ListGroup>
                </Card>
            </Col>
        </Row>

        </>
    
}

export default OrderScreen