import React from 'react'
import {Container,Navbar,Nav, NavDropdown} from 'react-bootstrap'
import {LinkContainer} from 'react-router-bootstrap'
import {useDispatch,useSelector} from 'react-redux'
import {logout} from '../actions/userActions'
import SearchBox from './SearchBox'

const Header = () => {
  const dispatch= useDispatch()

  const userLogin = useSelector(state => state.userLogin)
  const {userInfo} = userLogin

  const logoutHandler = () => {
    dispatch(logout())
  }

  return (
    <Navbar className="justify-content-between" bg="primary" variant="dark" expand="lg" collapseOnSelect style={{marginBottom:"10px"}}>
    <Container>
      <LinkContainer to="/">
        <Navbar.Brand>MyShop</Navbar.Brand>
      </LinkContainer>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <SearchBox />
        <Nav className="ms-auto">
          <LinkContainer to="/cart">
            <Nav.Link ><i className="fa-solid fa-cart-shopping"> </i> Cart</Nav.Link>
          </LinkContainer>
          {userInfo && userInfo.isAdmin && (
            <NavDropdown title='Admin' id="adminmenu">
                <LinkContainer to='/admin/userlist'>
                  <NavDropdown.Item>Users</NavDropdown.Item>
                </LinkContainer>
                <LinkContainer to='/admin/productlist'>
                  <NavDropdown.Item>Products</NavDropdown.Item>
                </LinkContainer>
                <LinkContainer to='/admin/orderlist'>
                  <NavDropdown.Item>Orders</NavDropdown.Item>
                </LinkContainer>
              </NavDropdown>
          )}
          {userInfo ? (
              <NavDropdown title={userInfo.name} id="username">
                <LinkContainer to='/profile'>
                  <NavDropdown.Item>
                    Profile
                  </NavDropdown.Item>
                </LinkContainer>
                <NavDropdown.Item onClick={logoutHandler}>Logout</NavDropdown.Item>
              </NavDropdown>
            ) :
            <LinkContainer to="/login">
              <Nav.Link ><i className="fa-solid fa-user"> </i> Sign-in</Nav.Link>
            </LinkContainer>
          }
          
        </Nav>
      </Navbar.Collapse>
    </Container>
    </Navbar>
  )
}

export default Header